import React, { Component } from "react";
import Button from 'bee-button';
import Grid from "../../src";
const columns = [
    {
        title:'',
        dataIndex:'index',
        width:'80',
        render:(text,record,index)=>{
            return index
        },
        fixed:'left'
    },
    {
    title: "", dataIndex: "a", key: "a", width: 580, className: "rowClassName",
  },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 80},
  { title: "", dataIndex: "c", key: "c", width: 200 }
];

const data = [ ...new Array(10000) ].map((e, i) => {
    const rs = { a: i + 'a', b: i + 'b', c: i + 'c', d: i + 'd', key: i };
    if(i%3==0){
        rs.b = '';
    }
    return rs;
   })

class Demo4 extends Component {
  constructor(props) {
    super(props);
  }
  //
  shouldComponentUpdate(){
    if(this.props.className =='u-panel-title'){
      return false;
    }
  }
  getSelectedDataFunc = data => {
    console.log("data", data);
  };

  selectedRow = (record, index) => {};
  /**
   * 
   */
  freshata = () => {

  }
  onDataNumSelect = () => {
    console.log('');
  }
  onRowHover = (index,record) => {
    this.currentIndex = index;
    this.currentRecord = record;
  }
  handleClick = () => {
    alert('' + this.currentIndex + ':' + this.currentRecord.a);
  }

  getHoverContent=()=>{
    return <div className="opt-btns"><Button size="sm" onClick={this.handleClick}></Button> </div>
  }
  render() {
    let paginationObj = {
      items:10,//
      total:100,//
      freshData:this.freshData,//
      onDataNumSelect:this.onDataNumSelect, //
      noBorder:true
    }
    return (
      <Grid
        className="demo"
        columns={columns}
        data={data}
        getSelectedDataFunc={this.getSelectedDataFunc}
        paginationObj={paginationObj}
        loadLazy={true}
        heigth={40}
        scroll = {{y:300}}
        hoverContent={this.getHoverContent}
        onRowHover={this.onRowHover}
      />
    );
  }
}
export default Demo4;


